<template>
  <div class="ebook" ref="ebook">
    <ebook-header />
    <ebook-title />
    <ebook-reader />
    <ebook-menu />
    <ebook-book-mark />
    <ebook-footer />
  </div>
</template>

<script>
import EbookReader from '@/components/EbookReader/EbookReader'
import EbookTitle from '@/components/EbookReader/EbookTitle'
import EbookMenu from '@/components/EbookReader/EbookMenu'
import EbookBookMark from '@/components/EbookReader/EbookBookMark'
import EbookHeader from '@/components/EbookReader/EbookHeader'
import EbookFooter from '@/components/EbookReader/EbookFooter'
import { getReadTime, saveReadTime } from '@/utils/localStorage'
import { ebookMixin } from '@/utils/mixin/ebookMixin'

export default {
  mixins: [ebookMixin],
  watch: {
    offsetY (v, o) {
      // 标题栏显示，以及分页未完成时，不允许下拉
      if (!this.menuVisible && this.bookAvailable) {
        if (v > 0) {
          this.move(v)
        } else if (v === 0) {
          this.restore()
        }
      }
    }
  },
  methods: {
    startLoopReadTime () {
      let readTime = getReadTime(this.fileName)

      if (!readTime) {
        readTime = 0
      }
      // 计算阅读时间
      this.tesk = setInterval(() => {
        readTime++
        if (readTime % 30 === 0) {
          saveReadTime(this.fileName, readTime)
        }
      }, 1000)
    },
    move (v) {
      this.$refs.ebook.style.top = v + 'px'
    },
    restore () {
      this.$refs.ebook.style.top = 0
      this.$refs.ebook.style.transition = 'all 0.2s ease-out'
      // 再次下拉出现卡顿现象，解决方案，使用setTimeout清除过渡动画
      setTimeout(() => {
        this.$refs.ebook.style.transition = ''
      }, 200)
    }
  },
  components: {
    EbookReader,
    EbookTitle,
    EbookMenu,
    EbookBookMark,
    EbookHeader,
    EbookFooter
  },
  mounted () {
    this.startLoopReadTime()
  },
  // 销毁之前清除定时器
  beforeDestroy () {
    if (this.tesk) {
      clearInterval(this.tesk)
    }
  }
}
</script>

<style lang="scss" scoped>
  .ebook {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
</style>
